import pay from "../../components/pay.vue"
export default {
	data(){
		return {
			pet:[],
			title:"编辑",
			isRemove:false,
			isAll:true,
			isRe:true,
			isAllPay:true,
			isPay:true,
			allPrice:0,
		}
	},
	components:{
		pay
	},
	onShow() {
		uni.getStorage({
			key:"pet",
			success:(res)=> {
				this.pet = res.data
				this.pet.forEach(item=>{
					item.isSelect = true
				})
				this.getAllPrice()
			},
			fail(err) {
				console.log(err)
				
			}
		})		
	},
	methods:{
		//编辑和取消切换
		titleTab(){
			this.getAllPrice()
			if(this.isRemove){
				this.title = "编辑"
				this.isRemove = false
			}else{
				this.title = "取消"
				this.isRemove = true
			}
		},
		//在可编辑页是否全选
		isAllClick(){
			this.isAll = !this.isAll
			this.isAllPay = !this.isAllPay
			if(this.isAll){
				this.pet.forEach(item=>{
					item.isSelect = true
				})
				this.getAllPrice()
			}else{
				this.pet.forEach(item=>{
					item.isSelect = false
				})
				this.getAllPrice()
			}
		},
		//在不可编辑页面是否全选
		isAllPayClick(){
			this.isAll = !this.isAll
			this.isAllPay = !this.isAllPay
			if(this.isAllPay){
				this.pet.forEach(item=>{
					item.isSelect = true
				})
				this.getAllPrice()
			}else{
				this.pet.forEach(item=>{
					item.isSelect = false
				})
				this.getAllPrice()
			}
		},
		//计算全部金额
		getAllPrice(){
			this.allPrice = 0
			this.pet.forEach(item=>{
				if(item.isSelect){
					this.allPrice += item.price
				}
			})
		},
		//单个商品处理
		itemPay(item,index){	
			item.isSelect = !item.isSelect
			this.$set(this.pet,index,item)
			this.getAllPrice()
			this.isAll = this.isAllPay = this.pet.every(p=>p.isSelect)
			this.isPay = this.isRe = this.pet.some(p=>p.isSelect)
		},
		//去支付
		pay(){
			let v = this.pet.filter(item=>item.isSelect)
			if(this.allPrice){
				this.$store.commit("setPay",v)
				this.$refs.pay.showPay = true
			}else{
				uni.showToast({
					title:"目前购物车为空！",
					image:"../../static/cry.png",
					duration:2000
				})
			}
			// this.$refs.pay.getPayList()
		},
		//删除商品
		remove(){
			this.pet = this.pet.filter((item,index)=>{
				return item.isSelect === false
			})
			uni.setStorage({
				key:"pet",
				data:this.pet,
				success:()=>{
					this.$store.commit("setPetByRemove",this.pet)
				},
				fail:function(err){
					console.log(err)
				}
			})
		},
		//去item详情页
		toItem(event,item){
			let e = event.touches[0]
			if(e.pageX>300){
				return 
			}
			uni.showLoading({
				title:'跳转中...',
				success:()=>{
					uni.navigateTo({
						url:`../item/item?id=${item.id}`,
						success:()=>{
							uni.hideLoading()
						}
					})		
				}
			})
			
		},
		//回到主会场
		toPath(){
			uni.showLoading({
				title:"跳转中..."
			})
			uni.switchTab({
				url:"../main/main",
				success:()=>{
					uni.hideLoading()
				}
			})
		}
	}
}
